/* Upload */

.preview {
	display: flex;
	flex-wrap: wrap;
	padding: .5rem;
}

.preview-image {
	margin-top: .5rem;
	margin-right: .5rem;
	position: relative;
	overflow: hidden;
}


.preview-image img {
	width: 180px;
	height: auto;
}

.preview-image:hover .preview-remove {
	opacity: 1;
}

.preview-remove {
	opacity: 0;
	width: 20px;
	height: 20px;
	position: absolute;
	right: 0;
	top: 0;
	font-weight: bold;
	display: flex;
	align-items: center;
	justify-content: center;
	background: rgba(248, 35, 35, 0.5);
	cursor: pointer;
	transition: opacity .22s;
}

.preview-image.removing {
	transform: scale(0);
	transition: transform .3s;
}

.preview-info {
	height: 24px;
	position: absolute;
	right: 0;
	bottom: -30px;
	left: 0;
	font-size: .8rem;
	display: flex;
	align-items: center;
	justify-content: space-between;
	background: rgba(115, 113, 113, 0.5);
	padding: 0 5px;
	transition: bottom .22s;

}

.preview-image:hover .preview-info {
	bottom: 0px;
}


