}

.contact-info-warp p {
	margin-bottom: 0;
	font-size: 22px;
	color: #fff;
	margin-right: 80px;
	display: inline-block;
}

.contact-info-warp p i {
	margin-right: 10px;
}

.contact-info-warp p:last-child {
	margin-right: 0;
}

.contact-right {
	padding-top: 40px;
	padding-left: 55px;
}

.contact-form input,
.contact-form textarea {
	width: 100%;
	border: 1px solid #ebebeb;
	margin-bottom: 17px;
	height: 40px;
	padding: 0 20px;
	font-size: 14px;
}

.contact-form textarea {
	height: 80px;
	padding: 15px 20px 10px;
}

/*------------------
	Responsive
---------------------*/

@media (min-width: 1200px) {
	.container {
		max-width: 1170px;
	}
}

/* Medium screen : 992px. */

@media only screen and (min-width: 992px) and (max-width: 1199px) {
	.filter-form input {
		width: calc(20% - 20px);
	}

	.filter-form select {
		width: calc(10% - 20px);
	}

	.footer-widget .double-menu-widget ul:last-child {
		margin-left: 30px;
	}

	.contact-info-warp {
		padding: 30px 20px;
	}

	.contact-info-warp p {
		font-size: 20px;
		margin-right: 20px;
	}
}

/* Tablet :768px. */

@media only screen and (min-width: 768px) and (max-width: 991px) {
	.header-top-right {
		padding-top: 20px;
	}

	.user-panel {
		float: right;
	}

	.main-menu li a {
		margin-left: 15px;
	}

	.hero-text p {
		font-size: 18px;
	}

	.filter-form input {
		width: calc(40% - 20px);
	}

	.filter-form select {
		width: calc(19% - 20px);
	}

	.filter-form .fs-submit {
		min-width: auto;
		width: 10%;
	}

	.propertie-info .price {
		float: left;
		margin-top: 15px;
	}

	.f-cata {
		margin-bottom: 30px;
	}

	.gallery-item.grid-wide {
		width: calc(50% - 20px);
	}

	.gallery-item {
		width: calc(50% - 20px);
	}

	.blog-item {
		margin-bottom: 30px;
	}

	.footer-widget .double-menu-widget {
		padding-left: 0;
	}

	.footer-bottom {
		text-align: center;
	}

	.footer-nav,
	.copyright {
		float: none;
	}

	.page-top-section.single-blog {
		padding-top: 200px;
	}

	.sidebar {
		padding-left: 15px;
		margin-top: 30px;
	}

	.team-member {
		margin-bottom: 30px;
	}

	.contact-right {
		padding-left: 0;
	}
}

/* Large Mobile :480px. */

@media only screen and (max-width: 767px) {
	.header-top-left {
		display: none;
	}

	.header-top-right {
		text-align: center;
	}

	.top-social a:first-child {
		margin-left: 0;
	}

	.site-navbar {
		position: relative;
		display: block;
	}

	.nav-switch {
		position: absolute;
		right: 0;
		font-size: 30px;
		color: #fff;
		top: 0;
		display: block;
		cursor: pointer;
	}

	.main-menu {
		position: absolute;
		top: 135%;
		left: 0;
		width: 100%;
		background: #fff;
		display: none;
		-webkit-box-shadow: 0 10px 20px 0px rgba(167, 167, 167, 0.22);
		box-shadow: 0 10px 20px 0px rgba(167, 167, 167, 0.22);
	}

	.main-menu li a {
		display: block;
		margin-left: 0;
		color: #707070;
		padding: 12px 20px;
		border-bottom: 1px solid #e1e1e1;
	}

	.hero-section {
		height: auto;
	}

	.hero-text {
		padding-top: 250px;
		padding-bottom: 150px;
	}

	.filter-form input,
	.filter-form select {
		width: 100%;
		margin-bottom: 20px;
	}

	.filter-form .fs-submit {
		margin: 0 auto;
		display: block;
	}

	.f-cata {
		margin-bottom: 30px;
	}

	.gallery {
		margin-right: 0;
	}

	.gallery-item.grid-long,
	.gallery-item.grid-wide,
	.gallery-item {
		width: 100%;
	}

	.blog-item {
		margin-bottom: 30px;
	}



	.footer-widget {
		margin-bottom: 20px;
	}

	.footer-widget .double-menu-widget,
	.footer-widget .contact-widget {
		padding-left: 0;
	}

	.footer-bottom {
		text-align: center;
	}

	.footer-nav,
	.copyright {
		float: none;
	}

	.sidebar {
		padding-left: 15px;
		margin-top: 30px;
	}

	.team-member {
		margin-bottom: 30px;
	}

	.blog-share {
		text-align: left;
		margin-bottom: 20px;
	}

	.blog-share h5 {
		margin-bottom: 10px;
	}

	.blog-share .share-links a {
		display: inline-block;
		margin: 0;
		margin-right: 10px;
	}

	.page-top-section.single-blog h2 {
		font-size: 25px;
	}

	/* .single-list-slider .sl-item {
		height: 300px;
	} */

	.sl-thumb-slider .sl-thumb {
		height: 90px;
	}

	.contact-info-warp p {
		margin-right: 30px;
	}

	.contact-right {
		padding-left: 0;
	}

	.about-text .about-text-left,
	.about-text .about-text-right {
		padding-right: 0;
	}
}


/* small Mobile :320px. */

@media only screen and (max-width: 479px) {
	.top-social {
		display: none;
	}

	.user-panel {
		margin-left: 0;
	}

	.user-panel a:first-child {
		border-left: none;
		padding-left: 0;
	}

	.page-top-section.single-blog {
		height: auto;
		padding-top: 170px;
